import { Routes } from '@interfaces/routes.interface';
import { Router } from 'express';

import ShiftController from '@/controllers/shift.controller';

import * as authenCheck from '@/middlewares/auth.check.authenticate.middleware';

class ShiftRoute implements Routes {
  public router = Router();
  public shiftController = new ShiftController();

  constructor() {
    this.initializeRoutes();
  }

  private initializeRoutes() {
    this.router.post(`/close/shift`, this.shiftController.closeShift);
    this.router.post(`/close/date`, this.shiftController.closeShift);
  }
}

// [authenCheck.requireAuthentication]
export default ShiftRoute;
